  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1111;
}
.preloader:before {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  width: 380px;
  height: 380px;
  content: "";
  display: block;
  background-color: #fff;
  border-radius: 50%;
  z-index: 1113;
}
@media only screen and (max-width: 560px) {
  .preloader:before {
    width: 300px;
    height: 300px;
  }
}
.preloader__outer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  width: 240px;
  height: 240px;
  border: 5px solid #093981;
  background: #fff;
  border-radius: 120px;
  box-sizing: border-box;
  position: relative;
  z-index: 1113;
}
.preloader__outer:after {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  content: " ";
  display: block;
  width: 100px;
  height: 110%;
  background: #fff;
  transform-origin: center;
  z-index: 0;
  animation: outerRotate2 30s infinite linear;
}
.preloader__inner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  width: 200px;
  height: 200px;
  color: #888;
  text-align: center;
  border: 5px solid #0e59c9;
  border-radius: 100px;
  box-sizing: border-box;
  z-index: 20;
  text-transform: uppercase;
}
.preloader__inner:after {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  content: " ";
  display: block;
  background: #fff;
  transform-origin: center;
  z-index: -1;
}
.preloader__inner:after {
  width: 100px;
  height: 106.3%;
  animation: outerRotate1 15s infinite linear;
}
.preloader__text {
  font-size: 1.4rem;
  line-height: 130px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  animation: textColour 1s alternate linear infinite;
  display: block;
  width: 140px;
  height: 140px;
  text-align: center;
  border: 5px solid #1a6fef;
  border-radius: 70px;
  box-sizing: border-box;
  z-index: 20;
}
.preloader__text:before, .preloader__text:after {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
  content: " ";
  display: block;
  background: #fff;
  transform-origin: center;
  z-index: -1;
}
.preloader__text:before {
  width: 110%;
  height: 40px;
  animation: outerRotate2 3.5s infinite linear;
}
.preloader__text:after {
  width: 40px;
  height: 110%;
  animation: outerRotate1 8s infinite linear;
}

