.tabs::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 88px;
  pointer-events: none;
  border-top: solid 1px #caccd3;
  border-bottom: solid 1px #caccd3;
}
@media only screen and (max-width: 767px) {
  .tabs::before {
    display: none;
  }
}
.tabs__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
@media only screen and (max-width: 767px) {
  .tabs__container {
    display: block;
  }
}
.tabs__item {
  width: 100%;
  max-width: 316px;
  display: flex;
  min-height: 88px;
  order: -1;
}
@media only screen and (max-width: 767px) {
  .tabs__item {
    max-width: none;
    margin-bottom: 10px;
    padding: 0;
    min-height: 0;
    border: solid 1px #caccd3;
  }
}
.tabs__item button {
  font-size: 15px;
  line-height: 1;
  color: #4f5764;
  font-weight: 500;
  background-color: transparent;
  cursor: pointer;
  transition: color 0.3s ease;
  appearance: none;
  align-self: center;
}
@media only screen and (max-width: 767px) {
  .tabs__item button {
    display: flex;
    width: 100%;
    padding: 21px 20px;
    font-size: 15px;
    line-height: 1.2;
    color: #2c60ae;
  }
  .tabs__item button::after {
    content: "";
    width: 8px;
    height: 4px;
    margin-left: 10px;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='8' height='4' viewBox='0 0 8 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='m0 0 4 4 4-4H0Z' fill='%23083880'/%3e%3c/svg%3e");
    align-self: center;
  }
  .tabs__item button span {
    align-self: center;
  }
}
@media (min-width: 1080px) {
  .tabs__item button:hover, .tabs__item button:focus {
    color: #2c60ae;
  }
}
.tabs__item.active button {
  font-size: 19px;
  color: #2c60ae;
  pointer-events: none;
}
@media only screen and (max-width: 767px) {
  .tabs__item.active button {
    font-size: 15px;
    pointer-events: auto;
  }
  .tabs__item.active button::after {
    transform: scale(1, -1);
